import React, { useEffect, useRef } from "react";
import { AiOutlineClose } from "react-icons/ai";
import { useAccount } from "../context/AccountContext";

function AddAccount({ show, handleClose, onAutoFocus }) {
  const nameRef = useRef();
  const maxRef = useRef();
  const { addAccount } = useAccount();

  const handleClear = () => {
    nameRef.current.value = "";
    maxRef.current.value = "";
  };

  useEffect(() => {
    nameRef.current.focus()
  }, [])


  const handleSubmit = (e) => {
    e.preventDefault();
    addAccount({
      name: nameRef.current.value,
      max: parseFloat(maxRef.current.value),
    });
    handleClose();
    handleClear();
  };
  return (
    <div className={`wrapper ${show ? "show" : "hide"}`}>
      <div
        className={`form__container container center ${show ? "show" : "hide"}`}
      >
        <form onSubmit={handleSubmit} className="form">
          <div className="form__heading flex">
            <h3 className="form__heading-title"> </h3>
            <button
              onClick={handleClose}
              className="form__heading-btn close-btn"
            >
              <AiOutlineClose />
            </button>
          </div>
          <div className="form__body">
            <div className="form__body-box">
              <label></label>
              <input
                ref={nameRef}
                type={"text"}
                name="account"
                id="name"
                className="form__body-control"
                required
              />
            </div>
            <div className="form__body-box">
              <label> </label>
              <input
                ref={maxRef}
                type={"number"}
                id="max"
                min={"0"}
                step="0.01"
                className="form__body-control"
                required
              />
            </div>
            <div className="form__body-btn">
              <button type="submit" className="btn">
                
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default AddAccount;
